@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Redressed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@800&display=swap);
.news_box {
  clear: both;
  padding-top: 10px;
  animation: fade-in 1s;
}

@keyframes fade-in{
  from{
    opacity: 0;
  }
  to  {
    opacity: 1;
  }
}

.mainer{
  text-align: center;
  font-family: 'Nanum Gothic', sans-serif;
}

.dev {
  text-align: center;
  font-family: 'Redressed', cursive;
}

.copy{
  text-align: center;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.name_today{
    font-size: 30px;
}
.name_cured{
    font-size: 30px;
}
.name_infected{
    font-size: 30px;
}
.name_death{
    font-size: 30px;
}
.box_today{
    margin-top: 20px;
    border-radius: 15px;
    margin-left: calc((100% - 300px)/2);
    border: 1px solid blue;
    background-color: blue;
    width: 300px;
    height: 200px;
    font-size: 80px;
    color: white;
    text-align: center;
}
.box_cured{
    margin-top: 20px;
    border-radius: 15px;
    margin-left: calc((100% - 300px)/2);
    border: 1px solid green;
    width: 300px;
    height: 200px;
    font-size: 80px;
    background-color: green;
    color: white;
    text-align: center;
}
.box_infected{

    margin-top: 20px;
    border-radius: 15px;
    margin-left: calc((100% - 300px)/2);
    border: 1px solid orange;
    width: 300px;
    height: 200px;
    font-size: 80px;
    background-color: orange;
    color: white;
    text-align: center;
}
.box_death{
    margin-top: 20px;
    border-radius: 15px;
    margin-left: calc((100% - 300px)/2);
    border: 1px solid red;
    width: 300px;
    height: 200px;
    font-size: 80px;
    background-color: red;
    color: white;
    text-align: center;
}

.covid_box{
    font-family: 'Nanum Gothic', sans-serif;
    clear: both;
    width: 100%;
    animation-duration: 1s;
    animation-name: updown;
    margin-top: 50px;
}

@keyframes updown{
    from {
        opacity: 0;
        margin-top: 0px;
    }
    to {
        opacity: 1;
        margin-top: 50px;
    }
}


